import Event from "../game/Event";
import React from "react";
import Icon from "../components/Icon";
import {EQUIPMENT_LASER} from "../data/resourceData";
import {PLANET_TYPE_GRAY, PLANET_TYPE_GREEN} from "../data/planetData";
import {SimpleLogExploration} from "./dataLogs";
import Feature from "../game/Feature";

class WildlifeExploration extends Event {
    calculate(mission) {
        return {
            title: 'Giant bugs',
            voice: `Danger, giant bugs! Exhaust a crew member.`,
            result: <React.Fragment>
                <p>Your team was attacked by a swarm of giant bug like creatures!</p>
                <p>It turns out that the creatures are afraid of lasers <Icon obj={EQUIPMENT_LASER} />. If you have one - you are safe. If you don't, exhaust one of your crew members.</p>
            </React.Fragment>,
        }
    }
}

export class WildlifeFeature extends Feature {
    dynamicEvents(planet) {
        const ret = [];

        if (planet.type === PLANET_TYPE_GRAY || planet.type === PLANET_TYPE_GREEN) {
            ret.push(new WildlifeExploration());
            ret.push(new WildlifeExploration());
        }

        return ret;
    }

    fillStaticData(universe, creator) {
        universe.dataLogs.push(new SimpleLogExploration({
            text: `There are bugs living on gray and green planets, which are really afraid of laser beams.`
        }));
    }
}