import Event from "../game/Event";
import React from "react";
import Icon from "../components/Icon";
import {CREW_TECHNICIAN} from "../data/resourceData";
import {PLANET_TYPE_RED} from "../data/planetData";
import {SimpleLogExploration} from "./dataLogs";
import Feature from "../game/Feature";

class DustStormExploration extends Event {
    calculate(mission) {
        return {
            title: 'Dust storm',
            result: <React.Fragment>
                <p>Your team was caught in a vicious dust storm, the metallic dust gets everywhere!</p>
                <p>If you have a technician <Icon obj={CREW_TECHNICIAN} />, you are safe. Exhaust two equipment cards otherwise.</p>
            </React.Fragment>,
            voice: `Danger, dust storm! Exhaust two equipment cards`
        }
    }
}

export class DustStormsFeature extends Feature {
    dynamicEvents(planet) {
        const ret = [];

        if (planet.type === PLANET_TYPE_RED) {
            ret.push(new DustStormExploration());
            ret.push(new DustStormExploration());
        }

        return ret;
    }

    fillStaticData(universe, creator) {
        universe.dataLogs.push(new SimpleLogExploration({
            text: `Red planets have violent dust storms, your equipment might break there. Technicians are good at keeping electronics up and running.`
        }));
    }
}
