import {shuffle} from "../misc/Utils";
import {returnToScanner} from "../components/Dialog";
import "./AwayMission.css";
import {createDemoDynamicEvents} from "../exploration/demoUniverse";
import {LandingExploration} from "../exploration/landing";

export class AwayMission {
    constructor(planet) {
        this.planet = planet;

        this.events = [
            ...planet.explorations,
            ...planet.staticDangers,
            ...createDemoDynamicEvents(planet),
        ];

        shuffle(this.events);
        this.insertNext(new LandingExploration());

        console.log(this.events);
    }

    insertNext(event) {
        this.events.unshift(event);
    }

    next() {
        console.assert(this.events.length > 0);
        let exploration = this.events.splice(0, 1)[0];
        exploration.onExplorationDialog(this);
    }

    return() {
        returnToScanner();
    }
}