import * as Serialisation from "../misc/Serialisation";
import React from "react";
import {Entity} from "./Entity";
import "./Location.css";
import {Universe} from "../game/Universe";
import App from "../components/App"
import {speak} from "../misc/Utils";
import {returnToScanner} from "../components/Dialog";

export class Location extends Entity {
    constructor({neighbourIds = new Set(), areTokensPlaced = false, sector = null, nebula = null, bodies = [], ...config}) {
        super(config);
        this.neighbourIds = neighbourIds;
        this.bodies = bodies;
        this.sector = sector;
        this.nebula = nebula;
        this.areTokensPlaced = areTokensPlaced;
    }

    get neighbours() {
        let ret = [];
        for (let neighbourId of this.neighbourIds) {
            let neighbour = Universe.theUniverse.entityForId(neighbourId);
            console.assert(neighbour !== null, "neighbour !== null");
            ret.push(neighbour);
        }
        return ret;
    }

    removeBody(item) {
        let index = this.bodies.indexOf(item);
        console.assert(index !== -1);
        this.bodies.splice(index, 1);
    }

    addBody(item) {
        item.locationId = this.id;
        this.bodies.push(item);
    }

    get nameWithHints() {
        //const info = this.nebula ? `at a cross section of ${this.sector} sector and the ${this.nebula} nebula` : `in the ${this.sector} sector`;
        const info = `in the ${this.sector} sector`;

        return `the ${this.name} system ${info}`
    }

    localScanningResult() {
        return <React.Fragment key={this.id}>
            <h1> {this.renderImage()} {this.name}</h1>
            <div className="celestial-body-token-list">
                {
                    this.bodies.map((body, i) =>
                        <div key={i} className={`celestial-view size-${body.size}`} style={{backgroundImage: `url(${body.image})`}} />
                    )
                }
            </div>
        </React.Fragment>
    }

    get isVicinityScanned() {
        if (this.areTokensPlaced) {
            return true;
        }

        for (const neighbour of this.neighbours) {
            if (neighbour.areTokensPlaced) {
                return true;
            }
        }

        return false;
    }

    markVicinityScanned() {
        this.areTokensPlaced = true;
        this.neighbours.forEach((neighbour) => {
            neighbour.areTokensPlaced = true;
        });
        Universe.notifyThatUniverseChanged();
    }

    scanArea() {
        speak("Scanning ...");

        App.COMPONENT.setDialog(<div className="dialog-info">
            <div className="dialog-inner">
                <div className="dialog-content star-system-details">
                    <h1>Scanning results</h1>
                    <p>Place the following objects on the map.</p>
                    {!this.areTokensPlaced && this.bodies.length > 0 ? this.localScanningResult() : null}
                    {
                        this.neighbours.map(neighbour =>
                            !neighbour.areTokensPlaced && neighbour.bodies.length > 0  ? neighbour.localScanningResult() : null
                        )
                    }
                </div>
                <div className="dialog-bottom">
                    <button onClick={() => {
                        this.markVicinityScanned();
                        this.onSelected();
                    }}>
                        OK
                    </button>
                    <button className="not-important" onClick={() => returnToScanner()}>
                        Back
                    </button>
                </div>
            </div>
        </div>);
    }

    onSelected() {
        speak(this.name);

        let neighbours = this.neighbours.map(neighbour => neighbour.name).join(", ").replaceLast(", ", " and ");

        if (this.isVicinityScanned) {
            //sorted bodies
            let bodies = this.bodies.sort((a, b) => b.actions.length - a.actions.length).map(body =>
                <div className={`celestial-info ${body.disabled ? ` disabled` : ``}`} key={body.id} >
                    <div className={`celestial-view size-${body.size}`} style={{backgroundImage: `url(${body.image})`}} />
                    <div className="celestial-actions">
                        {body.disabled ? body.disabled : null}
                        {body.actions.map(action =>
                            <button
                                key={action.name}
                                onClick={action.action}
                                className={`celestial-action action-${action.type} ${action.action ? "" : "celestial-action disabled"}`}>
                                {action.name}
                            </button>
                        )}
                    </div>
                </div>

            );

            //{this.name} is fully explored, try flying to another location and exploring there.
            App.COMPONENT.setDialog(<div className="dialog-info">
                <div className="dialog-inner">
                    <div className="dialog-content star-system-details">
                        <h1> {this.renderImage()} {this.name}</h1>
                        <p>{this.name} system is a system in the {this.sector} sector{this.nebula ? `, in the ${this.nebula} nebula` : `` }. It neighbours with {neighbours}.</p>
                        {bodies.length === 0 ? <p>No celestial bodies detected</p> : null}
                        {bodies}
                    </div>
                </div>
            </div>, false);
        } else {
            App.COMPONENT.setDialog(<div className="dialog-info">
                <div className="dialog-inner">
                    <div className="dialog-content">
                    </div>
                    <div >
                        <h1> {this.renderImage()} {this.name}</h1>
                        <p> This is an uncharted part of space, you need to first scan it.</p>
                        <button key="scan" onClick={() => this.scanArea()}>
                            Scan Area
                        </button>
                    </div>
                    <div className="dialog-content" />
                </div>
            </div>, false);
        }

    }
}

Serialisation.registerDynamicClass(Location, "Location");
