import Event from "../game/Event";
import React from "react";
import Icon from "../components/Icon";
import {RESOURCE_A, RESOURCE_B, RESOURCE_D} from "../data/resourceData";
import {SimpleLogExploration} from "./dataLogs";
import {getRandomElement, getRandomInt} from "../misc/Utils";
import * as Serialisation from "../misc/Serialisation";
import Feature from "../game/Feature";

class JustTradedEvent extends Event {
    constructor({resource, amount, ...config}) {
        super(config);

        this.resource = resource;
        this.amount = amount;
    }

    calculate(mission) {
        return {
            title: `Traders`,
            voice: `Trading ${this.resource.namedCount(this.amount)} for ${RESOURCE_D.namedCount(1)}`,
            result: <React.Fragment>
                <p>Discard {this.resource.namedCountWithIcon(1)} and receive {RESOURCE_D.namedCountWithIcon(1)}.</p>
            </React.Fragment>
        };
    }
}

class DidNotTradeEvent extends Event {
    calculate(mission) {
        return {
            title: `Traders`,
            voice: `No deal`,
            result: <React.Fragment>
                <p>You did not trade with the aliens, feel free to come back later once you will be willing to trade.</p>
            </React.Fragment>
        };
    }
}

class AlienTradersEvent extends Event {
    constructor({resource, amount, ...config}) {
        super(config);

        this.resource = resource;
        this.amount = amount;
    }

    calculate(mission) {
        return {
            title: `Traders`,
            voice: `You have encountered alien traders`,
            result: <React.Fragment>
                <p>You have encountered alien traders.</p>
                <p>You can exchange <Icon obj={this.resource} count={this.amount} /> to get <Icon obj={RESOURCE_D} count={1} />.</p>
                <p>If you have those resources you can trade right now.</p>
            </React.Fragment>,
            buttons: [
                <button key="yes" onClick={() => {
                    mission.planet.markExplored(this);
                    mission.insertNext(new JustTradedEvent({resource: this.resource, amount: this.amount}));
                    mission.next();
                }}>Trade</button>,
                <button key="no" onClick={() => {
                    mission.insertNext(new DidNotTradeEvent());
                    mission.next();
                }}>Don't</button>,
            ],
            markExplored: false,
        };
    }
}

Serialisation.registerDynamicClass(AlienTradersEvent, "AlienTradersEvent");

export class AlienTradersFeature extends Feature {
    dynamicEvents(planet) {
        return [];
    }

    fillStaticData(universe, creator) {
        const filterWithDescription = creator.possibleSources.pop();
        const resource = getRandomElement([RESOURCE_A, RESOURCE_B]);
        const amount = getRandomInt(2, 3);
        const exploration = new AlienTradersEvent({resource, amount});

        creator.addExplorationCandidates({
            exploration,
            minPerPlanet: 1,
            minTotal: 5,
            maxTotal: 7,
            filter: filterWithDescription.filter,
        });

        universe.dataLogs.push(new SimpleLogExploration({
            text: `Alien traders can be found ${filterWithDescription.description} and are trading artifacts for ${resource.plural}.`
        }));
    }
}
