import {StaticEvent} from "../game/Event";
import React from "react";
import {CELESTIAL_SIZE_MULTIPLIERS} from "../data/planetData";
import {EQUIPMENT_BUGGY} from "../data/resourceData";
import Icon from "../components/Icon";
import Feature from "../game/Feature";

export const EXPLORATION_TRAVEL = new StaticEvent({id: "EXPLORATION_TRAVEL", calculate: (zone) => {
        return {
            title: "Travel",
            result: <React.Fragment>
                <p>You spent hours traveling and haven't found anything of interest.</p>
                <p>Unless you have a <Icon obj={EQUIPMENT_BUGGY} />, exhaust one team member.</p>
            </React.Fragment>,
            voice: `You spent hours traveling and haven't found anything of interest. Exhaust one team member.`,
        }
    }});


export class TravelFeature extends Feature {
    dynamicEvents(planet) {
        let ret = [];
        for (let i = 0; i < CELESTIAL_SIZE_MULTIPLIERS[planet.size] - 1; i++) {
            ret.push(EXPLORATION_TRAVEL);
        }
        return ret;
    }

    fillStaticData(universe, creator) {

    }
}
