import React from "react";
import App from '../components/App';
import * as Serialisation from "../misc/Serialisation";
import {registerStaticObject} from "../misc/Serialisation";
import {speak} from "../misc/Utils";

export let exploreAndReturnToScanner = (zone) => {
    zone.markExplored();

    App.COMPONENT.setDialog(null);

    if (zone.planet.isFullyExplored) {
        zone.planet.onFullyExplored();
    }
};

export default class Event {
    constructor({providedResources = [], requiredResources = []} = {}) {

        this.providedResources = providedResources;
        this.requiredResources = requiredResources;
    }

    calculate(mission) {
        console.error("Not Implemented");

        return {};
    }

    onExplorationDialog(mission) {
        let {title = "???", voice, result = <React.Fragment>ERROR</React.Fragment>, buttons, markExplored = true} = this.calculate(mission);

        if (markExplored) {
            mission.planet.markExplored(this);
        }

        if (!voice) {
            voice = title;
        }

        if (!buttons) {
            buttons = [];

            let canExploreFurther = !mission.planet.isFullyExplored && mission.events.length > 0;

            if (canExploreFurther) {
                buttons.push(<button key="next" onClick={() => {
                    mission.next()
                }}>Explore</button>);
            }

            buttons.push(
                <button key="leave" className={canExploreFurther ? "not-important" : ""} onClick={() => {mission.return();}}>
                    Leave
                </button>
            );
        }

        if (!mission.planet.isFullyExplored) {
            speak(`${voice ? voice : ""}`);
        } else {
            speak(`${voice ? voice : ""}. ${mission.planet.name} is now fully explored.`);
        }

        let finalStatus = null;

        if (mission.planet.isFullyExplored) {
            finalStatus = <p>{mission.planet.name} is now fully explored.</p>
        }

        if (mission.events.length === 0) {
            finalStatus = <p>There is nothing more to do here at this moment.</p>
        }

        App.COMPONENT.setDialog(
            <div className="dialog" key={Math.random()} >
                <div className="dialog-inner">
                    <div className="dialog-content exploration">
                        <h1>{title}</h1>
                        {result}
                    </div>
                    <div className="dialog-bottom">
                        {finalStatus}
                        {buttons}
                    </div>
                </div>
            </div>
        );
    }
}

export class StaticEvent extends Event {
    constructor({id, calculate, providedResources = [], requiredResources = [], ...config} = {}) {
        super({
            ...config
        });

        this.id = id;
        this.providedResources = providedResources;
        this.requiredResources = requiredResources;
        this._calculate = calculate;

        registerStaticObject(id, this);
    }

    calculate(zone) {
        return this._calculate(zone);
    }
}

Serialisation.registerStaticClass(StaticEvent);